.advantages {
  padding-top: 40px;
  padding-bottom: 60px;
  background-color: $mainBgColor;
}

.contaier {
  width: 100%;
  padding-left: 20px;
  align-items: flex-start;
}

.advant__item {
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0px;
  }
}

.advant__namber {
  font-family: 'Titan One', sans-serif;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: $mainTextColor;

  min-width: 98px;
  height: 56px;
  margin-top: 16px;
  margin-bottom: 4px;
}

.advant__descr {
  font-family: 'DM Sans', sans-serif;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;
  color: $fourthTextColor;

  max-width: 230px;
}